<template>
  <div>
    <h4>请输入登录信息</h4>
    <input type="text" placeholder="请输入手机号" v-model="tel"><br>
    <input type="text" placeholder="请输入姓密码" v-model="name"><br>
    <button @click="login">登录</button>
  </div>
</template>
<script>
  export default{
    data(){
      return{
        tel:'',
        name:''
      }},
    methods:{
      login(){
        if(this.$store.state.admin.people[this.name] == this.tel){
          alert('登陆成功，欢迎来到个人中心')
           this.$store.state.admin.name=this.name
            this.$store.state.admin.tel=this.tel
          this.$router.push('/admin')
          this.$store.state.admin.check=true

        }else{
          alert('请输入正确信息！')
          this.name = ''
          this.tel = ''
        }

      }
    }

  }
</script>
<style>
</style>



