<template>
  <div>
    <li class="buy_list"  v-if="num>0"   v-for="(list,num) in $store.state.shopData.shop[index].food[this.$route.params.id2]">
      <div class="food_img"><img :src="list.pic"></div>
      <div class="buy_text"><h4 class="">{{list.title}}</h4><p>{{list.text}}</p><span>月售：{{list.sell}}</span><h3 class="red">￥：{{list.pay}}</h3>
      	<button @click="buy(list.pay,num)">+</button><span> {{list.num}} </span>
<button @click="del(list.pay,num)">-</button></div>

    </li>
    <div class="pay_all">￥：{{this.$store.state.shopData.shop[this.$store.state.shopData.a].money}} </div><button @click="goPay">去结算</button>
  </div>
</template>
<script>
  export default{
    data(){
      return{
      	num2:0,
        index:this.$route.params.id,
      }
    },
       mounted() {



    },
    methods:{
      buy(num,index){
        this.$store.state.shopData.shop[this.$store.state.shopData.a].money +=parseInt(num)
        this.$store.state.shopData.shop[this.index].food[this.$route.params.id2][index].num+=1
      },
      del(num,index){
      	if(this.$store.state.shopData.shop[this.index].food[this.$route.params.id2][index].num>0){
      		this.$store.state.shopData.shop[this.$store.state.shopData.a].money -=parseInt(num)
        this.$store.state.shopData.shop[this.index].food[this.$route.params.id2][index].num-=1
      	}

      },
      goPay(){
      var i=1
    while(i<this.$store.state.shopData.shop[this.index].food[this.$route.params.id2].length){
    	 if(this.$store.state.shopData.shop[this.index].food[this.$route.params.id2][i].num>0){
      	  this.$store.state.admin.order.unshift(this.$store.state.shopData.shop[this.index].food[this.$route.params.id2][i])


      }
    	i++
    }
      	console.log(this.$store.state.admin.order)
      		if(this.$store.state.admin.check==false){
  		alert('您好,请先登录!')
  		this.$router.push('/login')
  	}else{
  		this.$router.push('/buyNow')}
      }
    }
  }
</script>
<style>
</style>



