import Vue from 'vue'
import vuex from 'vuex'
Vue.use(vuex);

import adminData from '../components/admin.js';//引入某个store对象
import shop from '../components/shop.js';//引入某个store对象
export default new vuex.Store({
  modules: {
    admin: adminData,
    shopData:shop
  }
})



