<template>
  <div class="home">
    <mt-swipe :auto="4000">
      <mt-swipe-item><router-link :to="{path:'/Java'}"><img src="../assets/pic/images/center.jpg"></router-link></mt-swipe-item>
      <mt-swipe-item><router-link :to="{path:'/Web'}"><img src="../assets/pic/images/banner_23.jpg"></router-link></mt-swipe-item>
      <mt-swipe-item><router-link :to="{path:'/Ui'}"><img src="../assets/pic/images/timg.jpg"></router-link></mt-swipe-item>
    </mt-swipe>
    <ul class="nav_list">
      <li><router-link to="/shoplist"><img src="../assets/pic/1.png" alt=""> 美食</router-link></li>
      <li><router-link to="/shoplist"><img src="../assets/pic/2_03.png" alt="">美团超市</router-link></li>
      <li><router-link to="/shoplist"><img src="../assets/pic/2_05.png" alt="">生鲜果蔬</router-link></li>
      <li><router-link to="/shoplist"><img src="../assets/pic/2_07.png" alt="">下午茶</router-link></li>
      <li><router-link to="/shoplist"><img src="../assets/pic/2_13.png" alt="">校园优选</router-link></li>
      <li><router-link to="/shoplist"><img src="../assets/pic/2_15.png" alt="">美团专送</router-link></li>
      <li><router-link to="/shoplist"><img src="../assets/pic/2_16.png" alt="">快食简餐</router-link></li>
      <li><router-link to="/shoplist"><img src="../assets/pic/2_17.png" alt="">跑腿代购</router-link></li>
    </ul>
    <div class="banner"><img src="../assets/pic/images/banner_23.jpg" alt=""></div>
    <div class="cheap">
      <p>优惠推荐</p>
      <router-link  v-for="(list,id) in $store.state.shopData.shop" v-if="list.cheap" :to="'shopPage/'+id" >
        <li  class="list" @click="get(id)" >
          <div class="shop_pic"><img :src="list.logo"></div>
          <div class="text"><h4>{{list.name}}</h4>
            <span>月售：{{list.monthSell}}份---- {{list.time[0]}}分钟 {{list.time[1]}}km</span><br>
            <span>起售：{{list.place[0]}}份---- 配送：{{list.place[1]}} 人均：{{list.place[2]}}km</span></div>
        </li>
      </router-link>
    </div>
  </div>
</template>
<script>
  export default{
    methods:{
      get(num){
        this.$store.state.shopData.a =num
      }
    }
  }
</script>
<style>
</style>



