import Hello from '../components/HelloWorld.vue'
import home from '../pages/home.vue'
import login from '../components/login.vue'
import register from '../components/register.vue'
import serch from '../components/serch.vue'
import shopList from '../pages/shopList.vue'
import shopPage from '../pages/shopPage.vue'
import admin from '../pages/admin.vue'
import buyNow from '../pages/buyNow.vue'
import buyList from '../components/buyList.vue'
import serch2 from '../pages/serch.vue'
import buyList0 from '../components/buyList0.vue'
export default [
  {
    path:'/',
    component:home,
  },
  {
    path: '/home',
    component: home
  },
  {
    name:'people',
    path:'/people/:name',
    component:{
      template:'<div>我的姓名{{$route.params.name}}<br>我的学号{{$route.query.id}}</div>'
    }
  },
  {
    path:'/index',
    component:{
      template:'<div>首页购物内容</div>'
    }
  },
  {
    path:'/pay',
    component:{
      template:'<div>付款界面</div>'
    }
  },
  {
    path:'/login',
    component:login
  },
  {
    path:'/register',
    component:register
  },

  {
    path:'/admin',
    component:admin
  },
  {
    path:'/shopList',
    component:shopList
  },
    {
    path:'/buyNow',
    component:buyNow
  },
  {
    path: '/shopPage/:id',
    component: shopPage,
  },
  {
    path: '/serch',
    component: serch2,
  }

]



