<template>
<div>
  <div class="shop_top"><img :src="$store.state.shopData.shop[index].logo">
  <h4>{{$store.state.shopData.shop[index].name}}</h4></div>
  <li class="list" v-for="(list,num) in $store.state.shopData.shop[index].food">
    <div class="food_img"><img :src="list.pic"></div>
      <div class="buy_text"><h4 class="">{{list.title}}</h4><p>{{list.text}}</p><span>月售：{{list.sell}}</span><h3 class="red">￥：{{list.pay}}</h3>
      	<button @click="buy(list.pay,num)">+</button><span> {{list.num}} </span>
<button @click="del(list.pay,num)">-</button></div>

    </li>
    <div class="pay_all">￥：{{this.$store.state.shopData.shop[this.$store.state.shopData.a].money}} <button @click="goPay">去结算</button></div>
  </div>
</template>
<script>
  export default{
    data(){
      return{
      	num2:0,
      	shopData:this.$store.state.shopData,
        index:this.$route.params.id,
      }
    },
       mounted() {

    },
    methods:{
      buy(num,index){
        this.shopData.shop[this.shopData.a].money +=parseInt(num)
        this.shopData.shop[this.index].food[index].num+=1
      },
      del(num,index){
      	if(this.shopData.shop[this.index].food[index].num>0){
      		this.shopData.shop[this.shopData.a].money -=parseInt(num)
        this.shopData.shop[this.index].food[index].num-=1
      	}

      },
      goPay(){
      var i=0
      var b={}
    while(i<this.shopData.shop[this.index].food.length){
    	 if(this.shopData.shop[this.index].food[i].num>0){

    	 for(var x in this.shopData.shop[this.index].food[i]){
    	   b[x]=this.shopData.shop[this.index].food[i][x]
    	 }

      	this.$store.state.admin.order.push(b)
      	this.shopData.shop[this.index].food[i].num=0

      }

    	i++
    }

   this.$store.state.shopData.shop[this.$store.state.shopData.a].money=0
      		if(this.$store.state.admin.check==false){
  		alert('您好,请先登录!')
  		this.$router.push('/login')
  	}else{
  		this.$router.push('/buyNow')}
      }
    }
  }
</script>
<style>
</style>



