<template>
	<div class="admin">{{$store.state.admin.tel}}--欢迎来到这里
	<p id="c">
			收货姓名：{{$store.state.admin.mz}}<button onclick="c.style.display='none',d.style.display='block'">编辑</button></p>
		<p id="d">
			<input type="text" :placeholder="mz" v-model="mz"/><button onclick="c.style.display='block',d.style.display='none'" @click='xm'>确定</button></p>

		<p id="a">

			收货地址：{{$store.state.admin.dz}}<button onclick="a.style.display='none',b.style.display='block'">编辑</button></p>
		<p id="b">

		<input type="text" :placeholder="address" v-model="address"/><button onclick="a.style.display='block',b.style.display='none'" @click='gb'>确定</button></p>



    <router-link to="/login"><button @click="out">退出</button></router-link>
	</div>
</template>

<script>
  export default{
    data(){
      return{
      	address:'',
      	mz:''
      }
    },
     mounted() {

     },
    methods:{
    	gb(){
    		this.$store.state.admin.dz=this.address
    	},
    		xm(){

    		this.$store.state.admin.mz=this.mz
    	},
     out(){
      this.$store.state.admin.check=false
      }
    }
  }
</script>
<style>
</style>



