<template>
	<div>

		<div v-if="$store.state.admin.check">
		<p class="get">姓名：{{$store.state.admin.mz}}<br>
			电话：{{$store.state.admin.tel}}<br>
			地址：{{$store.state.admin.dz}}</p>
		<h4 class="anwer red">请确定以下订单</h4>
		<li class="order_list" v-for="(list,id) in $store.state.admin.order">

			<div class="food_img"><img :src="list.pic"></div>
			<div>
			<h4>{{id+1}}.{{list.title}}</h4>
			<span> 单价{{list.pay}} ×{{list.num}}  共{{list.pay*list.num}}元</span>
			<button @click="del(id)">取消</button>
	       </div>

		</li>

		<div v-if="allmoney>0"><div class="all">共{{allmoney}}元</div><button @click='zfb'>立即支付</button></div>
		<div v-else>您的购物车里还没有商品，点击<router-link to='shopList'>立即购买</router-link>吧</div>
		</div>
		<div v-else>
			<h4></h4>
			<h4>请登录后查看</h4>
			<router-link to="/login"><button>我要登录</button></router-link>
		</div>
	</div>
</template>

<script>
  export default{
    data(){
      return{
      	allmoney:0
      }
    },
     mounted() {
     	var i=0
     	while(i<this.$store.state.admin.order.length){
     		 this.allmoney+=this.$store.state.admin.order[i].num*this.$store.state.admin.order[i].pay
     		 i++
     	}
    },
    methods:{
    	zfb(){
    		alert('功能开发中')
    	},
    	del(index){

          this.allmoney-=this.$store.state.admin.order[index].num*this.$store.state.admin.order[index].pay
    	this.$store.state.admin.order.splice(index, 1)
    	}
    }
  }
</script>
<style>
</style>



