

<template>
  <div>
    <h4>请输入注册信息</h4>
    <input type="text" placeholder="请输入手机号" v-model="tel"><br>
    <input type="text" placeholder="请输入密码" v-model="name"><br>
    <button @click="login">注册</button>
  </div>
</template>
<script>
  export default{
    data(){
      return{
        tel:'',
        name:''
      }},
    methods:{
      login(){
        if(this.tel && this.name){
          this.$store.state.admin.people[this.name] =this.tel
          alert('注册成功！即将返回登录页面')
          this.$router.push('/login')
        }else{
          alert('请输入正确信息！')
        }

      }
    }

  }
</script>



